
import { defineComponent } from "vue";
import { DashboardConstants } from "@/dashboard/constants";
import BaseListing from "@/components/BaseListing.vue";
import TableActionContent from "@/components/TableActionContent.vue";

export default defineComponent({
  name: DashboardConstants.NAME.DASHBOARD_LIST_CONTAINER,
  components: {
    BaseListing,
    TableActionContent
  },
  data() {
    return {
      columns: [
        {
          field: "id",
          title: "ID",
          sortable: true,
          cell: "tooltipTemplate",
        },
        {
          field: "firstName",
          title: "First Name",
          sortable: false,
          cell: "tooltipTemplate",
        },
        {
          field: "lastName",
          title: "Last Name",
          sortable: true,
          cell: "tooltipTemplate",
        },
        {
          field: "action",
          title: "Action",
          cell: "actionTemplate",
        },
      ],
      dashboardDataList: new Array<any>(),
    };
  },
  created() {
    this.dashboardDataList = [
      {
        id: 1,
        firstName: "Test",
        lastName: "User",
      },
      {
        id: 2,
        firstName: "Test2",
        lastName: "User2",
      },
      {
        id: 11,
        firstName: "Test11",
        lastName: "User11",
      },
      {
        id: 21,
        firstName: "Test21",
        lastName: "User21",
      },
    ];
  },
  methods: {
    addData() {
      this.$router.push({name: DashboardConstants.ROUTE.DATA})
    }
  }
});
